@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200;300;400;500;600;700&family=Open+Sans:wght@300;400;600&display=swap);
:root {
  --black: rgb(47, 48, 51);
  --black2: rgba(44, 44, 44, 0.966);
  /* --black2: rgba(85, 88, 99, 0.966); */
  --white: #f8f8f8;
  --gold: rgb(200,165,106);
}
  
* {
  box-sizing: border-box;
  margin: 0;
  text-decoration: none;
}

::-webkit-scrollbar {
  width: 0.5rem;
  background: rgba(44, 44, 44, 0.966);
  background: var(--black2);
}

::-webkit-scrollbar-thumb {
  background: rgb(200,165,106);
  background: var(--gold);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #f8f8f8;
  background: var(--white);
  
}

body {
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(47, 48, 51);
  background-color: var(--black);
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

button {
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  padding: 0.7rem;
  border: none;
  border-radius: 5px;
}

button:hover{
  background-color: rgb(200,165,106);
  background-color: var(--gold);
}
  
.footer {
    width: 100%;
    height: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--gold);
    font-size: 0.8rem;
    padding: 3rem 0;
    background-color: var(--black2);
    border-top: 1px solid var(--gold)
  }
  
.reactLogo {
  font-size: 1.2rem;
  margin-left: .2rem;
}

.home-wrapper {
  width: 100%;
}

.heroSection {
  color: var(--white);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10rem 0;
  width: 100%;
}
  
.nameAboutWrapper{
  width: 85%;
}
  
.fullName {
  font-family: 'Josefin Sans', sans-serif;
  font-size: 2.7rem;
  font-weight: 500;
}
  
.fullName p {
  margin: 0 0 .5rem;
  text-align: left;
}
  
.about {
  font-size: 1rem;
  text-align: center;
  margin: 3.5rem 0 0;
}

.socialWrapper {
  padding: 4rem 1rem 0;
}

.socialWrapper a {
  margin: 1rem;
}
  
.social {
  font-size: 1.8rem;
  flex-basis: 15%;
}

.linkedin,
.github {
  color: var(--white);
  cursor: pointer;
}

.linkedin:hover,
.github:hover {
  color: var(--gold);
}

.featured-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--black2);
  color: var(--white);
  padding: 5rem 0 5rem;
}

.featured-container h2 {
  font-size: 2rem;
  margin-bottom: 2.5rem;
}

.featured-container h3 {
  font-size: 1.1rem;
  font-weight: 400;
  padding: 0 2rem;
  margin-bottom: 4rem;
}
  
@media (min-width: 411px) {

  .fullName {
    font-size: 3.4rem;
  }
  
  .about {
    font-size: 1.05rem;
  }

  .social {
    font-size: 2.2rem;
  }
}
  
@media (min-width: 768px) {

  .nameAboutWrapper{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;

  }

  .fullName {
    font-size: 3.6rem;
  }

  .about {
    margin: 0;
    width: 17rem;
    padding-bottom: 1rem;
    font-size: 1.2rem;
  }
  
  .social {
    flex-basis: 4rem;
  }
} 
  
@media (min-width: 1024px) {

  .fullName {
    font-size: 5rem;
  }

  .about {
    width: 20rem;
    font-size: 1.4rem;   
  }

  .social {
    font-size: 2.5rem;
  }

  .featured-wrapper {
    display: flex;
  }

  .featured-container h3 {
    font-size: 1.2rem
  }

}
  
@media (min-width: 1440px) {

  .nameAboutWrapper{
    width: 90%;
    margin-top: 2rem;
  }

  .fullName {
    font-size: 8rem;
  }

  .about {
    width: 23rem;
    font-size: 1.6rem;   
    padding-bottom: 3rem;
  }

  .socialWrapper{
    padding: 4.5rem 0 0;
  }

  .social {
    font-size: 2.7rem;
    flex-basis: 5rem;
  }
}
  
@media (min-width: 1877px) {
  
  .nameAboutWrapper{
    width: 70%;
  }
}
.skills {
    color: var(--white);
    padding: 5rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.skills h2 {
    font-size: 2rem;
    padding-bottom: 2.5rem;
}

.skills-logos {
    padding: 0 2rem;
}

/* .skills-logos img:first-child{
    padding-top: 0.5rem;
} */

.skills-logos img {
    height: 50px;
    margin: 2rem 1rem;
}

.skills-logos img:first-child {
    height: 50px;
}

@media only screen and (min-width: 768px){
    .skills-logos {
        max-width: 35rem;
    }
}

@media only screen and (min-width: 1024px) {
    .skills-logos {
        max-width: 55rem;
    }

    .skills-logos img {
        height: 80px;
        margin: 2rem 1.5rem;
        max-width: 55rem;
    }
    
    .skills-logos img:first-child {
        height: 80px;
        
    }

}
.featured {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 0 2rem;
}

.featured img {
    position: relative;
    width: 20rem;
    height: 20rem;
    border-radius: 5px;
}

.featured-description {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: var(--black);
    transition: opacity .3s ease;
    opacity: 0;
    width: 20rem;
    height: 20.2rem;
    border-radius: 5px;
}

.featured-description p {
    max-width: 19rem;
    padding-bottom: 2rem;
}

.featured:hover .featured-description {
    opacity: 1;
}

@media (min-width: 411px) {
    .featured img {
        width: 23rem;
        height: 23rem;
    }

    .featured-description {
        width: 23rem;
        height: 23.2rem;
    }
}

@media (min-width: 1024px) {
    .featured {
        margin: 2rem;
    }
} 


.title-container {
    margin: 6rem 0 6rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

h1 {
    font-size: 2rem;
    color: var(--white);
    font-family: 'Josefin Sans', sans-serif;
}

.icons-container {
    width: 12rem;
    font-size: 3rem;
    color: var(--white);
    margin-bottom: 1rem;
}

.icons {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-bottom: 1rem;
}
.projects-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-bottom: 5rem;
    /* margin: 3rem;
    padding: 2rem;
    box-shadow: inset 3px 3px 3px rgba(0, 0, 0, 0.432), inset -3px -3px 3px rgba(0, 0, 0, 0.432) ;
    border-radius: 10px; */
}

.bootcamp {
    font-size: 1.5rem;
    padding-left: .5rem;
}
.card-wrapper {    
    width: 19rem;
    height: 23rem;
    margin: 1.5rem 1rem;
    color: var(--white);
    position: relative;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.233) 1px 3px 5px 1px, rgba(0, 0, 0, 0.233) -1px 3px 5px 1px;
    background-color: rgba(0, 0, 0, 0.151);
    background-color: var(--black2);
}

.img-wrapper{
    position: relative;
    width: 100%;
}

.img-wrapper img {
    width: 100%;
    border-radius: 5px 5px 0 0 ;
}

.description-wrapper{
    display: flex;
    position: absolute;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 0;
    width: 100%;
    overflow: hidden;
    transition: .3s ease;
    background-color: rgba(47, 48, 51, 0.877);
    border-radius: 5px 5px 0 0;
    text-align: center;
    -webkit-transform: translate(0%, -100%);
    transform: translate(0%, -100%);
}

.card-wrapper:hover .description-wrapper {
    height: 20rem;
}

.card-wrapper:hover .title {
    color: var(--gold);
}

.title {
    padding-top: 1rem;
    font-size: 1.2rem;
    color: var(--white);
}

.description {
    font-size: 1.1rem;
    padding: 0 1rem 3rem;
    
}

.project-links {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
}

.github {
    width: 2rem;
    height: auto;
}

.btn {
    cursor: pointer;
}

.card-anchor,
.card-anchor:link,    
.card-anchor:visited {
    color: black;
    text-decoration: none;
}

.project-links {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
}

@media (min-width: 375px) {
    .card-wrapper {    
        width: 22.5rem;
        height: 27rem;  
    }
    .card-wrapper:hover .description-wrapper {
        height: 23rem;
    }
}

@media (min-width: 411px) {
    .card-wrapper {    
        width: 24rem;
        height: 28.5rem;  
    }
    .card-wrapper:hover .description-wrapper {
        height: 25rem;
    }
}

@media (min-width: 1024px) {
    .card-wrapper {  
        margin: 1.5rem;
    }
}
header {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 5rem;
    width: 100%;;
    color: var(--gold);
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: var(--black2);
    border-bottom: 1px solid var(--gold);
    z-index: 1;
}

.nav-wrapper {
    display: flex;
    width: 90%;
    justify-content: center;
    align-items: center;
}

.home, .projects, .contact {
    width: 7rem;
    font-size:0.9rem;
    font-weight: bold;
}

.nav-wrapper div:hover {
    color: var(--white);
}

.nav-wrapper a {
    color: var(--gold);
}

.projects {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.projects > * {
    margin: 0 .2rem;
}

.open-dropdown {
    position: absolute;
    top: calc(100% + 1.93rem);
    background-color: var(--black2);
    border-left: 1px solid var(--gold);
    border-right: 1px solid var(--gold);
    border-bottom: 1px solid var(--gold);
    border-radius: 0 0 5px 5px;
    width: 12rem;
    padding: 1.5rem 0 .5rem 0;
}

.open-dropdown p {
    padding-bottom: 1.5rem;
    color: var(--white);
}

.open-dropdown p:hover {
    color: var(--gold);
}

@media only screen and (min-width: 1024px) {

}
